.card-five-btn {
  border-radius: 0 20px 20px 0;
  background-color: #000;
}

.card-five__icon {
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  width: 30px;
  height: 30px;
}
.card-five__title {
  font-weight: 400;
  font-size: 16px;
  color: #000;
}

.card-five__input {
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  width: 150px;
  height: 35px;
}
